import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const Scheduler = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/147075921-8f2ab7e7-0e6a-4389-8e0d-70892c278574.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147076333-a5ac535d-a5fa-4e1e-af67-e2094d4149ce.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147076385-d22c0af1-5df6-49e0-ab75-b06af39562ef.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147076439-44979767-3640-4df7-98fd-c5bae845227e.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147076239-16dd79d6-6290-4d87-aa3f-531d9c821877.png'}
    ]
    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">Scheduler</Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    // showThumbs={false}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About Scheduler</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies Used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">Laravel</Typography></li>
                            <li><Typography variant="body2" color="initial">Bootstrap</Typography></li>
                            <li><Typography variant="body2" color="initial">JQuery</Typography></li>
                            <li><Typography variant="body2" color="initial">AJAX</Typography></li>
                            <li><Typography variant="body2" color="initial">MySQL</Typography></li>
                            <li><Typography variant="body2" color="initial">Google Identity API</Typography></li>
                        </ul>
                        <Typography textAlign="justify">
                            General Scheduling system. Powered with Google Identity API and Google Charts API integration.
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="http://scheduler-app-tech.herokuapp.com/" target="_blank">http://scheduler-app-tech.herokuapp.com/</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            GitHub Repository : <a href="https://github.com/rocknrold/Scheduler" target="_blank">https://github.com/rocknrold/Scheduler</a>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default Scheduler;