import { Container, Dialog, DialogContent, Divider, Grid, Paper, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useState } from "react";
import { Link } from "react-router-dom";

const AboutMe = () => {

    document.title = 'rocknrold | About Me'

    const certs = [
        {name:'sdlc-cert', link:'https://s3.amazonaws.com/coursera_assets/meta_images/generated/CERTIFICATE_LANDING_PAGE/CERTIFICATE_LANDING_PAGE~TZR5K5XSPPHU/CERTIFICATE_LANDING_PAGE~TZR5K5XSPPHU.jpeg'},
        {name:'linuxdev-cert', link:'https://s3.amazonaws.com/coursera_assets/meta_images/generated/CERTIFICATE_LANDING_PAGE/CERTIFICATE_LANDING_PAGE~QYZRXNB8938R/CERTIFICATE_LANDING_PAGE~QYZRXNB8938R.jpeg'},
    ]

    const [open, setOpen] = useState(false);
    const [src, setSrc] = useState(null);

    const handleClickOpen = (e) => {
      setOpen(true);
    //   console.log(e.target.attributes.getNamedItem('data-index').value);
        setSrc(certs[e.target.attributes.getNamedItem('data-index').value]);
    };
  
    const handleClose = () => {
      setOpen(false);
    };

    return (
        <Container maxWidth={false}>
            <Grid container spacing={0} justifyContent="center">
                <Paper sx={{m:'10%', p:6, borderRadius:'12px'}} elavation={12}>
                    <Link to='/'><i className="fas fa-angle-left"><i className="fas fa-angle-left"></i></i></Link>
                    <Box sx={{pt:2, maxWidth:600}}>
                        <Typography variant="h4">
                            About Me
                        </Typography>
                        <Divider/>
                        <Box sx={{ pt:2, pb:2}}>
                            <Paper sx={{backgroundColor:'#f5f5f5', p:4}} elavation={0}>
                                <Typography variant="subtitle1" textAlign="center" lineHeight={2} letterSpacing={0.5} color="#757575">
                                    <i>I aim to attain an engaging role in the field of Information Technology. 
                                    I am willing to help create and contribute to the success of projects as well 
                                    as the company I am engaged in.</i><br/>
                                    <Typography variant="body2" textAlign="center" lineHeight={2}>
                                - <i>Aaron, Developer</i>
                                </Typography>
                                </Typography>
                            </Paper>
                        </Box>
                        <Divider/>
                        <Box sx={{background:'#f5f5f5', mt:2, p:1}}>  
                            <Typography variant="h6" textAlign="center">
                                Certificates
                            </Typography>
                        </Box>
                        {
                            certs.map((cert,index) => {
                                return (
                                <Tooltip title="Click to expand" placement="top" key={cert.name}>
                                    <img onClick={(e) => { handleClickOpen(e) }} data-index={index} name={cert.name} src={cert.link} alt={cert.name} width="100%" />
                                </Tooltip>
                                )
                            })
                        }
                        <Dialog
                            open={open}
                            onClose={handleClose}
                            aria-labelledby="alert-dialog-title"
                            aria-describedby="alert-dialog-description"
                            width="100%"
                        >
                            <DialogContent>
                                {
                                src && <img width="1000px" src={src.link} alt={src.name}/>
                                }
                            </DialogContent>
                        </Dialog>
                    </Box>
                </Paper>
            </Grid>
        </Container>
    );
}
 
export default AboutMe;