import { Container, Divider, Grid, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";
import EnergyCounter from '../components/Projects/EnergyCounter';
import Aflex from "./Projects/Aflex";
import ReCite from "./Projects/ReCite";
import Scheduler from "./Projects/Scheduler";
import Sitcom from "./Projects/Sitcom";
import Sky from "./Projects/Sky";

const Projects = () => {
    
    document.title = 'rocknrold | Projects'

    return (
        <Container maxWidth={false}>
            <Grid container spacing={0} justifyContent="center">
                <Paper sx={{m:'10%', p:6, borderRadius:'12px', maxWidth: { lg:'100%', m:600, sm:300, xs:300}}} elavation={12}>
                    <Link to='/'><i className="fas fa-angle-left"><i className="fas fa-angle-left"></i></i></Link>
                        <Box sx={{pt:2}}>
                            <Typography variant="h4">
                                Projects
                            </Typography>
                            <Divider/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <ReCite/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <Aflex/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <EnergyCounter/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <Scheduler/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <Sitcom/>
                        </Box>
                        <Box sx={{ pt:2 }}>
                            <Sky/>
                        </Box>
                </Paper>
            </Grid>
        </Container>
    );
}
 
export default Projects;