import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const ReCite = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/114821491-3c58d300-9df3-11eb-904e-d9dad42d2537.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821498-3f53c380-9df3-11eb-966c-90131cb6e348.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821514-42e74a80-9df3-11eb-9094-41ef8bd3d055.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821526-45e23b00-9df3-11eb-9bf3-4814f43e7904.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821531-47abfe80-9df3-11eb-8a82-e46e530d301a.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821539-4aa6ef00-9df3-11eb-8d5d-706225e23886.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821549-4d094900-9df3-11eb-8a9b-faa735b2006e.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821555-4f6ba300-9df3-11eb-900c-0591d85bf5c4.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/114821563-52669380-9df3-11eb-96d0-203754b8bbdb.png'},
    ]

    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">ReCite Research Citation</Typography>
                <Typography variant="subtitle1" textAlign="center" color="#636464"><i>Open access to world researches, articles, and journals</i></Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    // showThumbs={false}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About Recite : Research Citation</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies Used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">Laravel</Typography></li>
                            <li><Typography variant="body2" color="initial">Material UI</Typography></li>
                            <li><Typography variant="body2" color="initial">JQuery</Typography></li>
                            <li><Typography variant="body2" color="initial">AJAX</Typography></li>
                            <li><Typography variant="body2" color="initial">MySQL</Typography></li>
                        </ul>
                        <Typography textAlign="justify">
                            This web application was made and developed for people, especially in the academe 
                            who’s financially short. This application allows the users to freely navigate through the available 
                            resources it can found across the internet in search of related studies, journals and even articles. 
                            Another powerful feature of this application is that it can cite various documents by the use of 
                            DOI (Digital Object Identifier). It is made possible by the help of various platforms in we 
                            development such as PHP, JQuery, MySQL etc. And with the help of different Application 
                            Programming Interface (API) possible without the open-source application programming interface 
                            (API) such as Core Services, Oxford Dictionary, OpenCitation and DataCite.
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="" target="_blank">N/A</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            GitHub Repository : <a href="https://github.com/rocknrold/ReCite-Research-Citation" target="_blank">https://github.com/rocknrold/ReCite-Research-Citation</a>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default ReCite;