import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const Aflex = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/147208604-d159ae95-379f-4b6e-bf22-3c90a7375fb9.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208659-30a468fc-e7e7-496f-be2c-e20bd9c28131.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208206-2ea378cd-8f74-46af-bcff-889ba1a8202f.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208255-fb1543ff-d832-41f8-b242-0169d28c55a0.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208316-fce08840-3af3-4cbb-b32d-8d1a136f3ec7.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208358-2b14cb33-874d-4c1a-ae29-c946f4a9e804.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208390-1f2bac22-871e-49d9-a0c8-b9f2206b555a.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208840-9bae18fc-9cdc-4469-bc1b-75fcefc6aed5.png' },
        { src : 'https://user-images.githubusercontent.com/43779189/147208999-543b8781-ab7a-42c8-a148-894c551ba0b7.png' }
    ]
    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">AFLEX</Typography>
                <Typography variant="subtitle1" textAlign="center" color="#636464"><i>Movie, Films, TV Shows flix</i></Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    // showThumbs={false}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About Aflex</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies Used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">MongoDB</Typography></li>
                            <li><Typography variant="body2" color="initial">Express</Typography></li>
                            <li><Typography variant="body2" color="initial">React Js</Typography></li>
                            <li><Typography variant="body2" color="initial">Node Js</Typography></li>
                            <li><Typography variant="body2" color="initial">Cloudinary</Typography></li>
                            <li><Typography variant="body2" color="initial">Netlify</Typography></li>
                            <li><Typography variant="body2" color="initial">Google Identity API</Typography></li>
                        </ul>
                        <Typography textAlign="justify">
                            AFLEX is a full MERN stack project for movies, films, tv show repositories and reference.
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="https://aflex.netlify.app/" target="_blank">https://aflex.netlify.app/</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            GitHub Repository : <a href="https://github.com/rocknrold/pinoy-flix">https://github.com/rocknrold/pinoy-flix</a>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default Aflex;