import { Avatar, Button, Chip, Container, Grid, Paper, Stack, Tooltip, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Link } from 'react-router-dom';
import QueryAbout from "./extras/QueryAbout";


const Home = () => {

    document.title = 'rocknrold.'

    const social = [
        { label: <i className="fab fa-github fa-2x"></i>, link: 'https://github.com/rocknrold', tooltip: 'GitHub profile of Aaron' }, 
        { label: <i className="fab fa-stack-overflow fa-2x" style={{color:'#F48024'}}></i>, link: 'https://stackoverflow.com/users/13042396/rocknrold', tooltip: 'StackOverflow profile of Aaron' },
        // { label: <span style={{ color: "white"}}><i className="fab fa-instagram fa-2x" style={{paddingLeft:'0.1em', paddingRight: '0.1em',borderRadius: '5px' ,background: "radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%), radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%), radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent), linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%)"}}></i></span>, link: 'https://www.instagram.com/haroldaaron.c/', tooltip: 'Instagram profile of Aaron' },
    ] 

    return (
        <Container maxWidth={false}>
            <Grid container spacing={0} justifyContent="center">
            <Paper sx={{m:'10%', p:6, borderRadius:'12px'}} elavation={12}>
                <Grid container spacing={0} justifyContent="center">
                <Grid item lg={8} md={8} sm={12} xs={12}>
                    <Typography variant="h2">
                    🐱‍💻 Hello, I'm Aaron
                    <Typography variant="caption">
                        [a - a - ron]
                    </Typography>.
                    </Typography>
                    <Box sx={{ pt:3,maxWidth:600}}>
                    <Typography textAlign="justify" variant="body1" lineHeight={1.8} letterSpacing={1}>
                        <i>An experienced software developer with various exposures from small to large-scale enterprise applications, 
                            skilled in web/mobile and software application development that utilizes different technologies and frameworks. 
                            Proficient in back-end and server-side programming with agile work in mind.</i>
                    </Typography>
                    </Box>
                    <Stack spacing={2} direction="row" sx={{pt:2, pb:2}}>
                    <Button variant="outlined" sx={{borderRadius:'12px', textTransform:'lowercase'}}>
                        <Link to='/projects' style={{ textDecoration:'none'}}>projects</Link>
                    </Button>      
                    <Button variant="outlined" sx={{borderRadius:'12px', textTransform:'lowercase'}}>
                        <Link to='/aboutme' style={{ textDecoration:'none'}}>about me</Link>
                    </Button>      
                    <Button variant="outlined" sx={{borderRadius:'12px', textTransform:'lowercase'}}>
                        <Link to='/contacts' style={{textDecoration:'none'}}>contacts</Link>
                    </Button>      
                    </Stack>
                </Grid>
                <Grid item lg={4} md={4} sm={12} xs={12}>
                    <Box sx={{ p:1 }}>
                        <img src="./Aaron.jpg" alt="Profile" style={{ borderRadius:'50%', maxWidth:'70%', display:'block', margin:'auto'}} />
                    </Box>
                    <Box sx={{pt:2}}>
                        <Stack spacing={1} direction="row" justifyContent="center">
                        {
                        social.map(media => {
                            return (
                            <Tooltip title={media.tooltip} key={media.tooltip}>
                                <a href={media.link} style={{ textDecoration:'none', color:'black'}}>
                                <span style={{ padding:'5px'}}>{media.label}</span>
                                </a>
                            </Tooltip>
                            )
                            })
                        }
                        </Stack>
                    </Box>
                </Grid>
                </Grid>
                <Box sx={{pt:2}}>
                    <QueryAbout/>
                </Box>
            </Paper>
            </Grid>
        </Container>
    );
}
 
export default Home;