import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const Sitcom = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/147080304-c430a26b-c9f4-47f0-b5e4-8891bb22f739.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199161424-f58f0b64-35e1-4884-b0cb-2926660e2696.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199163695-34a671be-5dec-452f-bc8c-6075aefa0dc2.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199164185-ed8efb9c-4b8d-4f69-bdb2-6c8a662c3b12.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199164444-7d887586-d35a-4d1c-9f97-0f54dd41314a.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199164816-db194060-59e9-4139-83e7-a7d7cc1b29c0.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199165019-daf1438c-3318-4cd5-8035-a2eba0b8c377.png'},
        // { src : 'https://user-images.githubusercontent.com/43779189/199165165-c1eb9e94-b8a8-4236-8181-049c   fb001339.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199165430-8deb647e-b261-4f0c-a30c-36da449b02ef.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199167739-05ea8d3d-03bf-4c40-bf4b-e4390f6afc6e.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199167886-48f58eb9-d4a5-4ff5-99a9-b4e2c149e91a.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199168172-97da0420-dce1-497a-bab5-2b9eee94a233.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/199169221-1c996760-9ded-4791-a052-09bab3cb8aec.png'},
    ]
    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">SITCOM - Supervised Industrial Training Computerized Organizational Monitoring</Typography>
                <Typography variant="subtitle2" textAlign="center" color="red"><i>status: completed</i></Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About SITCOM</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies being used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">Laravel</Typography></li>
                            <li><Typography variant="body2" color="initial">React JS</Typography></li>
                        </ul>
                        <Typography textAlign="justify">
                        Students receive on-the-job training to be prepared for their professional prospects. As students complete their training, they are observed, and such documentation is retained as a valuable resource and record of their progress. However, record keeping and monitoring using manual and conventional methods may be unavoidably challenging like losing some records of the trainees and difficulties in interaction among the trainees, the coordinator, and management. Hence, the researcher's main objective was to create a system that would enhance and computerize the management and monitoring of the internship program at the Technological University of the Philippines - Taguig Campus, allowing students, supervisors, and coordinators to connect while conducting the internship program until the students render the required hours and complete all the requirements. To test the effectiveness of the system, an online survey and demonstration were carried out. Respondents were asked to answer a set of survey questions based on the functionalities by the system upon their usage. Subsequently, responses were analyzed using mean and the Likert scale method. After a thorough analysis, the findings revealed that the system can replace the manual monitoring of internship with all the available tools and communication features integrated in the proposed system. Also, this system provides numerous functionalities like messaging and grade computation. However, this system has many chances for development, improvement, and the addition of new features.
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="https://sitcom-sit.herokuapp.com/" target="_blank">https://sitcom-sit.herokuapp.com/</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            {/* GitHub Repository : <a href="https://github.com/rocknrold/Scheduler" target="_blank">https://github.com/rocknrold/Scheduler</a> */}
                            GitHub Repository : <i style={{color:"red"}}>private repository</i>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default Sitcom;