import { Container, Divider, Grid, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";

const Contact = () => {
    
    document.title = 'rocknrold | Contact'

    return (
        <Container maxWidth={false}>
            <Grid container spacing={0} justifyContent="center">
                <Paper sx={{m:'10%', p:6, borderRadius:'12px'}} elavation={12}>
                    <Link to='/'><i className="fas fa-angle-left"><i className="fas fa-angle-left"></i></i></Link>
                    <Box sx={{pt:2, maxWidth:600}}>
                        <Typography variant="h4">
                            Contact
                        </Typography>
                        <Divider/>
                        <Box sx={{backgroundColor:'#f8f9fa', mt:2,p:2}}>
                            <Typography variant="body1" textAlign="center" color="#636464">
                                The following are my active accounts that can be used to contact me and talk about things that will become beneficial for both of us.
                            </Typography>
                        </Box>
                        <Box sx={{ mt:1}}>
                            <Typography variant="h5">
                            <i className="fas fa-envelope"></i> Email
                            </Typography>
                        </Box>
                        <Box sx={{backgroundColor:'#f8f9fa', mt:1,p:2}}>
                            <Typography variant="h6" textAlign="center" color="#636464">aaronharoldc@gmail.com</Typography>
                            {/* <Typography variant="h6" textAlign="center" color="#636464">harold.aaron@tup.edu.ph</Typography> */}
                        </Box>
                        {/* <Box sx={{ mt:1}}>
                            <Typography variant="h5">
                            <i className="fab fa-instagram"></i> Instagram
                            </Typography>
                        </Box>
                        <Box sx={{backgroundColor:'#f8f9fa', mt:1,p:2}}>
                            <Typography variant="h6" textAlign="center" color="#636464">haroldaaron.c</Typography>
                        </Box> */}
                        <Box sx={{ mt:1}}>
                            <Typography variant="h5">
                            <i className="fas fa-location-arrow"></i> Address
                            </Typography>
                        </Box>
                        <Box sx={{backgroundColor:'#f8f9fa', mt:1,p:2}}>
                            <Typography variant="h6" textAlign="center" color="#636464">Taguig City, Metro Manila, Philippines</Typography>
                        </Box>
                    </Box>
                </Paper>
            </Grid>
        </Container>
    );
}
 
export default Contact;