import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const Sky = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/199181765-35476e09-5981-45b3-a870-d7ef36d1d108.png'},
    ]
    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">SKY CABLE CORPORATION</Typography>
                <Typography variant="subtitle2" textAlign="center" color="red"><i>status: deployed to production</i></Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About Sky Cable project</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies being used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">Cannot be specified</Typography></li>
                            {/* <li><Typography variant="body2" color="initial">React JS</Typography></li> */}
                        </ul>
                        <Typography textAlign="justify">
                            Sky Cable Corporation project deployed on production.
                        </Typography><br/>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="https://www.mysky.com.ph/" target="_blank">https://www.mysky.com.ph/</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            {/* GitHub Repository : <a href="https://github.com/rocknrold/Scheduler" target="_blank">https://github.com/rocknrold/Scheduler</a> */}
                            {/* GitHub Repository : <i style={{color:"red"}}>private repository</i> */}
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default Sky;