import "react-responsive-carousel/lib/styles/carousel.min.css"; 
import { Carousel} from 'react-responsive-carousel';
import { Accordion, AccordionDetails, AccordionSummary, Paper, Typography } from "@mui/material";
import { Box } from "@mui/system";


const EnergyCounter = () => {

    const images = [
        { src : 'https://user-images.githubusercontent.com/43779189/147050970-84bd6536-daf4-4d42-9dc1-30c4abda26d0.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147051183-fafa94a7-023a-4be5-9e3d-e4495bac1d9d.png'},
        { src : 'https://user-images.githubusercontent.com/43779189/147051267-014b5f36-cf5b-4418-9822-694d15f8f274.png'},
    ]

    return (
        <>
            <Box sx={{backgroundColor:'#f8f9fa', mt:1, mb:1, p:2}}>
                <Typography variant="h5" textAlign="center" color="#636464">Simple Axie Infinity Energy Counter</Typography>
            </Box>
            
            <Paper elevation={6} sx={{ p:1 }}>
                <Carousel
                    autoPlay
                    interval={3000}
                    showArrows={true}
                    // showThumbs={false}
                    >
                    {images.map((image, index) => (
                        <div key={index}>
                            <img src={image.src}/>
                        </div>
                    ))}
                </Carousel>
            </Paper>

            <Box sx={{pt:1}}>
            <Accordion>
                    <AccordionSummary
                    expandIcon={<i className="fas fa-angle-down"></i>}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    >
                    <Typography variant="subtitle1">About Simple Axie Energy Counter</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography variant="body1" color="initial">
                            Technologies Used:
                        </Typography>
                        <ul>
                            <li><Typography variant="body2" color="initial">React JS</Typography></li>
                            <li><Typography variant="body2" color="initial">Redux</Typography></li>
                            <li><Typography variant="body2" color="initial">React Bootstrap</Typography></li>
                        </ul>
                        <Typography>
                            This simple energy counter is made purposely to help on counting the enemy's energy
                            during the game. It has a simple intuitive UI so you can start using it right away. 
                        </Typography><br/>
                        <Typography>
                            This application has been used by my colleagues and co-axie players, 
                            and has been praised for it's usefullness and simplicity during games.
                        </Typography>
                        <Typography variant="body1" color="initial">
                            Live website : <a href="https://simple-energy-counter.herokuapp.com/" target="_blank">https://simple-energy-counter.herokuapp.com/</a>
                        </Typography>
                        <Typography variant="body1" color="initial">
                            GitHub Repository : <a href="https://github.com/rocknrold/energy-counter" target="_blank">https://github.com/rocknrold/energy-counter</a>
                        </Typography>
                    </AccordionDetails>
                </Accordion>
            </Box>
        </>
    );
}
 
export default EnergyCounter;